.navi-bar{
    width: 100%;
    height: 70px;
    /* margin-bottom: 100px; */
    position: fixed;
    display: flex;
    background-color: hsl(0, 100%, 100%);
    z-index: 999;
    margin-top: 0;
    padding-top: 10px;
    color: rgb(105, 105, 105);
    box-shadow:1px 1px 5px rgb(197, 197, 197);
}

/* .scrolled {
    position: fixed;
} */

#signup{
    display: none;
}

.userhere{
    margin-top: 30px;
    font-weight: 600;
    color: black !important;
}

#collapsible-nav-dropdown {
    color: black;
}

.title{
    font-weight: 800;
    font-size: 25px;
    color: black !important;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.nav-links {
    margin-right: 20px;
}

.account-icon{
color:rgb(17, 17, 17);
margin-top: 10px;
margin-right: -5px;
margin-left: 10px;
}

.nav-icons{
    color: "black";  
}

.chaticon{
margin-top: 10px;
color:rgb(56, 55, 55);
font-weight: 500;
cursor: pointer;
display: block;
}

.chaticon2{
    margin-right: 2px;
    color: rgb(48, 48, 48);
    font-size: 11px !important;
    /* margin-left: 20px; */
    /* margin-top: 2px; */
}

.post-img{
    width: 100px;
    height: 100px;
    display: inline-flex;
    border-radius: 15px;
}

.post-title{
    display: inline-flex;
    margin-left: 10px;
    padding-top: 20px;
}

.card-body{
    background-color: rgb(255, 255, 255);
    border-radius: 15px;
    border-top-left-radius: 0%;
    border-top-right-radius: 0%;
}

.details-post{
    display: inline-flex;
}


@media screen and (max-width: 900px) {
    .nav-linkList {
       width: 100%;
    }

    .navi-bar {
        min-height: 70px;
        height: fit-content;
    }
}

